import React, { Component } from "react";
import axios from "axios";
import Flex from "components/shared-components/Flex";
import {
	Tag,
	Card,
	Table,
	Dropdown,
	Tooltip,
	Button,
	Modal,
	Layout,
	Row,
	Col,
	Input,
} from "antd";
import { SPACER } from "constants/ThemeConstant";
import {
	FileExcelOutlined,
	PrinterOutlined,
	ReloadOutlined,
	EllipsisOutlined,
	CheckOutlined,
	CloseOutlined,
} from "@ant-design/icons";
import AvatarStatus from "components/shared-components/AvatarStatus";

const { Header, Footer, Sider, Content } = Layout;
const { TextArea } = Input;

const transactionOption = [
	{
		key: "Refresh",
		label: (
			<Flex alignItems="center" gap={SPACER[2]}>
				<ReloadOutlined />
				<span className="ml-2">Refresh</span>
			</Flex>
		),
	},
	{
		key: "Print",
		label: (
			<Flex alignItems="center" gap={SPACER[2]}>
				<PrinterOutlined />
				<span className="ml-2">Print</span>
			</Flex>
		),
	},
	{
		key: "Export",
		label: (
			<Flex alignItems="center" gap={SPACER[2]}>
				<FileExcelOutlined />
				<span className="ml-2">Export</span>
			</Flex>
		),
	},
];

const CardDropdown = ({ items }) => {
	return (
		<Dropdown menu={{ items }} trigger={["click"]} placement="bottomRight">
			<a
				href="/#"
				className="text-gray font-size-lg"
				onClick={(e) => e.preventDefault()}
			>
				<EllipsisOutlined />
			</a>
		</Dropdown>
	);
};

export class UserReferralsComponent extends Component {
	state = {
		refers: [],
		refer: null,
		isModalOpen: false,
		rejectReasonValue: "",
	};

	componentDidMount() {
		axios.get("http://localhost:5000/api/management/allRefers").then((res) => {
			console.log(res.data);
			this.setState({
				refers: res.data.allRefers,
			});
		});
	}

	handleStatus = (refer, newStatus, comment) => {
		const newRefer = {
			...refer,
			status: newStatus,
			statusComment: comment,
		};
		console.log(newRefer);
		axios
			.post("http://localhost:5000/api/management/changeReferStatus", newRefer)
			.then((res) => {
				this.setState((prevState) => ({
					refers: prevState.refers.map((item) =>
						item.id === newRefer.id ? newRefer : item
					),
				}));
				this.handleCancel();
			})
			.catch((err) => console.log(err));
	};

	showModal = (refer) => {
		this.setState({
			isModalOpen: true,
			refer: refer,
			rejectReasonValue: refer.statusComment ?? "",
		});
	};

	handleOk = () => {
		this.handleStatus(
			this.state.refer,
			"rejected",
			this.state.rejectReasonValue
		);
	};

	handleCancel = () => {
		this.setState({ isModalOpen: false });
	};

	onChangeRejectReason = (e) => {
		this.setState({
			rejectReasonValue: e.target.value,
		});
	};

	render() {
		const { refers } = this.state;
		const defaultAvatar = "/img/avatars/default.png";
		const tableColumns = [
			{
				title: "User",
				dataIndex: "name",
				render: (_, record) => (
					<div className="d-flex">
						<AvatarStatus
							src={record?.avatar !== "" ? record.avatar : defaultAvatar}
							name={record.user}
							subTitle={record.email}
						/>
					</div>
				),
				sorter: {
					compare: (a, b) => {
						a = a.user.toLowerCase();
						b = b.user.toLowerCase();
						return a > b ? -1 : b > a ? 1 : 0;
					},
				},
			},
			{
				title: "Link",
				dataIndex: "link",
				key: "link",
			},
			{
				title: "Refer User",
				dataIndex: "name",
				render: (_, record) => (
					<div className="d-flex">
						<AvatarStatus
							src={
								record?.referUserAvatar !== ""
									? record.referUserAvatar
									: defaultAvatar
							}
							name={
								record?.referUserName !== ""
									? record.referUserName
									: "Not referred"
							}
							subTitle={record.referUserEmail}
						/>
					</div>
				),
				sorter: {
					compare: (a, b) => {
						a = a.user.toLowerCase();
						b = b.user.toLowerCase();
						return a > b ? -1 : b > a ? 1 : 0;
					},
				},
			},
			{
				title: () => <div className="text-right">Status</div>,
				key: "status",
				render: (_, record) => (
					<div className="text-right">
						<Tooltip title={record.statusComment}>
							<Tag
								className="mr-0"
								color={
									record.status === "approved"
										? "cyan"
										: record.status === "pending"
										? "blue"
										: "volcano"
								}
							>
								{record.status}
							</Tag>
						</Tooltip>
					</div>
				),
			},
			{
				title: <div className="block text-right pr-3">Actions</div>,
				dataIndex: "actions",
				render: (_, elm) => (
					<div className="text-right d-flex justify-content-end">
						<Tooltip title="approve">
							<Button
								className="mr-2"
								type="primary"
								icon={<CheckOutlined />}
								onClick={() => {
									this.handleStatus(elm, "approved", "");
								}}
								disabled={elm.status === "approved"}
							/>
						</Tooltip>
						<Tooltip title="block">
							<Button
								className="mr-2"
								type="primary"
								icon={<CloseOutlined />}
								onClick={() => {
									this.showModal(elm);
								}}
								disabled={elm.status === "rejected"}
								danger
							/>
						</Tooltip>
					</div>
				),
			},
		];

		return (
			<>
				<Row>
					<Col xs={24} sm={24} md={24} lg={24}>
						<Card
							title="All Referrals"
							extra={<CardDropdown items={transactionOption} />}
						>
							<Table
								className="no-border-last"
								columns={tableColumns}
								dataSource={refers}
								rowKey="id"
								pagination={false}
							/>
						</Card>
					</Col>
				</Row>

				<Modal
					open={this.state.isModalOpen}
					onOk={this.handleOk}
					onCancel={this.handleCancel}
					// okButtonProps={{ disabled: true }}
					okButtonProps={{
						disabled: this.state.rejectReasonValue === "",
					}}
					centered
					width="40%"
				>
					<Layout>
						<Header style={{ height: "60px", padding: "0 10px" }}>
							<p style={{ fontSize: "28px", marginBottom: "0px" }}>
								Add a Reject Reason
							</p>
						</Header>
						<Content style={{ padding: "20px" }}>
							<TextArea
								value={this.state.rejectReasonValue}
								onChange={this.onChangeRejectReason}
								placeholder="Please enter the reject reason."
								autoSize={{ minRows: 3, maxRows: 5 }}
							/>
						</Content>
					</Layout>
				</Modal>
			</>
		);
	}
}

export default UserReferralsComponent;
